body {
  font-family:
    -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu,
    Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: grid;
  grid-template-columns: 350px 1fr;
  gap: 30px;
  padding: 20px;
  max-width: 1400px;
  margin: auto;
}
#controls-container {
  border: 1px solid #ddd;
  padding: 0 20px 20px 20px;
  border-radius: 8px;
}
#output-container {
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
}
form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  align-items: center;
}
label {
  font-weight: 500;
  text-align: right;
}
input[type="number"],
select {
  width: 100%;
  box-sizing: border-box;
  padding: 6px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
button {
  grid-column: 1 / -1;
  padding: 12px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
}
h3 {
  border-bottom: 2px solid #eee;
  padding-bottom: 10px;
  grid-column: 1 / -1;
}
.description {
  grid-column: 1 / -1;
  font-size: 0.9em;
  color: #555;
  background-color: #f9f9f9;
  padding: 10px;
  border-radius: 4px;
}
canvas {
  border: 1px solid #ccc;
  width: 100%;
  max-width: 800px; /* 그래프가 너무 커지는 것 방지 */
  aspect-ratio: 1/1;
  margin: auto;
}
#results-output {
  background: #f4f4f4;
  padding: 15px;
  margin-top: 20px;
  border-radius: 5px;
  font-family: "Courier New", Courier, monospace;
  font-size: 14px;
  white-space: pre-wrap;
}
